import React, {Component} from 'react';
import PropTypes from "prop-types";
import LinceResultsMainPanel from "./LinceResultsMainPanel";
import {connect} from 'react-redux';
import {Button, Card, Col, Row} from "antd";
import {
    fetchContingencyMatrix,
    fetchKappaIndex,
    fetchKappaProIndex,
    fetchProfileInfo,
    fetchRegisterById
} from '../../redux/lince/videoActions';
import ShowNotification, {showError} from "../utility/ShowNotification";
import IsoWidgetsWrapper from "@iso/containers/Widgets/WidgetsWrapper";
import StickerWidget from "@iso/containers/Widgets/Sticker/StickerWidget";
import StyledResizable from "./StyledResizable";

class LinceResultsObserverSelector extends Component {
    constructor(props) {
        super(props);
        this.state = {expanded: false};
        this._onExpand = this._onExpand.bind(this);
        this.onExpand = this.props.onExpand.bind(this);
    }

    _onExpand() {
        const nextValue = !this.state.expanded;
        this.setState({expanded: nextValue});
        this.onExpand();
    }

    render() {
        const white = '#ffffff';
        const green = '#7ED320';
        const fontColor = this.state.expanded ? white : green;
        const bgColor = this.state.expanded ? green : white;
        const title = 'Observer ' + this.props.user ? this.props.user.userName : '- No name - ';
        const icon = this.state.expanded ? 'ion-person' : 'ion-person-add';
        const text = <><span>{title}</span>
            <Button onClick={this._onExpand} type="dashed" size={"small"} style={{float: "right"}}>Select</Button>
        </>
        return (
            <Row gutter={0} justify="center">
                <Col md={24} style={{paddingTop: "5px"}}>
                    <IsoWidgetsWrapper onClick={this._onExpand} style={{cursor: "pointer"}}>
                        {/* Sticker Widget */}
                        <StickerWidget number={this.props.user.registerAmount + " scenes"}
                                       text={text}
                                       icon={icon}
                                       fontColor={fontColor}
                                       bgColor={bgColor}
                        />
                    </IsoWidgetsWrapper>
                </Col>
                {/*    <Col md={8} style={{paddingTop: "15px"}}>
                    <Button onClick={this._onExpand}>Select</Button>
                </Col>*/}
            </Row>
            /*   <Card style={{marginTop: 5}} type="inner" title={title}
                     className={expanded}
                     extra={<a className={selected} onClick={this._onExpand}>Select</a>}>
                   <p>
                       {this.props.user.registerAmount}&#160;
                       <Trans i18nKey="results.scenesWithoutMarks">Scenes registered.</Trans>
                   </p>
               </Card>*/
        );
    }
}

LinceResultsObserverSelector.propTypes = {
    user: PropTypes.object.isRequired,
    onExpand: PropTypes.func
};

class LinceResultsKappaLauncher extends Component {
    constructor(props) {
        super(props);
        this.state = {users: [], currentUser: "", showResults: false};
        this.fetchRegisterById = this.props.fetchRegisterById.bind(this);
        this._onExpand = this._onExpand.bind(this);
        this._onActionRequest = this._onActionRequest.bind(this);
    }

    componentWillMount() {
        this.props.fetchProfileInfo();
    }

    _onActionRequest() {
        //console.log(this.props.currentAction);
        //const resultsPanelComponent = this.refs["resultsPanel"];
        let selected = [];
        this.props.profile.userProfiles.forEach(i => {
            let currentStatusActive = this.refs["item-" + i.key].state.expanded;
            if (currentStatusActive) {
                selected.push(i.registerCode);
            }
        });
        if (selected.length === 2) {
            //console.log(selected);
            ShowNotification('Observers', 'Se está calculando el índice Kappa de los registros seleccionados');
            this.setState({showResults: true});
            if (this.props.currentAction.key === "kappa") {
                this.props.fetchKappaIndex(selected[0], selected[1]);
            }
            if (this.props.currentAction.key === "kappa-pro") {
                this.props.fetchKappaProIndex(selected[0], selected[1]);
            }
            if (this.props.currentAction.key === "matrix") {
                this.props.fetchContingencyMatrix(selected[0], selected[1]);
            }
        } else {
            showError('', <p>Debes seleccionar 2 registros para poder lanzar los índices kappa.<br/>
                Los registros seleccionados tienen el botón de color verde y el observador como destacado</p>);
        }
    }

    _onExpand(u) {
        //console.log(u);
        this.props.fetchRegisterById(u.registerCode);
        this.setState({currentUser: u.userName, showResults: false});
    }

    render() {
        let users = [];
        if (this.props.profile.userProfiles) {
            users = this.props.profile.userProfiles;
        }
        console.log(this.props.kappaProIndex);
        return (
            <Row>
                <Col md={24} sm={24} xs={24}>
                    <div style={{display: 'flex', width: '100%', height: '100%'}}>
                        <StyledResizable defaultSize={{width: '35%', height: '100%'}}
                                         minWidth="300"
                                         minHeight="600">
                            <Card title="Participants of the project">
                                <Card type="inner">
                                    <div>
                                        <Row>
                                            <Col md={20}>
                                                <p>Found {users.length}
                                                    participants.{/*Se han encontrado {users.length} participantes.*/}</p>
                                                <p>You must select two participants to calculate your Kappa index or
                                                    contingency matrix
                                                    {/*Debes seleccionar dos participantes para poder calcular su índice Kappa o tabla de
                                contigencia*/}</p>
                                                <p>Krippendorf allows you 2 or more
                                                    observers{/*Krippendorf te permite 2 o más observadores*/}</p>
                                            </Col>
                                            <Col md={4}>
                                                <Button onClick={this._onActionRequest}>
                                                    Execute
                                                </Button>
                                            </Col>
                                        </Row>
                                    </div>
                                </Card>
                                {users.map((v) =>
                                    <LinceResultsObserverSelector key={v.key}
                                                                  ref={"item-" + v.key}
                                                                  user={v}
                                                                  isActive={false}
                                                                  onExpand={() => this._onExpand(v)}/>)
                                }
                            </Card>
                        </StyledResizable>
                        <div style={{width: "100%"}}>
                            <LinceResultsMainPanel ref="resultsPanel"
                                                   kappaResult={this.props.kappaIndex}
                                                   kappaProResult={this.props.kappaProIndex}
                                                   contingencyMatrix={this.props.contingencyMatrix}
                                                   showResults={this.state.showResults}
                                                   currentAction={this.props.currentAction}
                                                   name={this.state.currentUser}
                                                   registerContent={this.props.registerTemp}/>
                        </div>
                    </div>
                </Col>
            </Row>
        );
    }
}

LinceResultsKappaLauncher.propTypes = {
    name: PropTypes.string,
    currentAction: PropTypes.object.isRequired,
    profile: PropTypes.object.isRequired,
    selectedUsers: PropTypes.array,
    registerTemp: PropTypes.array,
    kappaIndex: PropTypes.array,
    kappaProIndex: PropTypes.array,
    contingencyMatrix: PropTypes.array,
    fetchProfileInfo: PropTypes.func.isRequired,
    fetchRegisterById: PropTypes.func.isRequired,
    fetchKappaIndex: PropTypes.func.isRequired,
    fetchKappaProIndex: PropTypes.func.isRequired,
    fetchContingencyMatrix: PropTypes.func.isRequired,
};
const mapStateToProps = state => ({
    profile: state.lince.profile,
    registerTemp: state.lince.registerTemp,
    kappaIndex: state.lince.kappaIndex,
    kappaProIndex: state.lince.kappaProIndex,
    contingencyMatrix: state.lince.contingencyMatrix
});
export default connect(mapStateToProps, {
    fetchProfileInfo,
    fetchRegisterById,
    fetchKappaIndex,
    fetchKappaProIndex,
    fetchContingencyMatrix
}, null, {forwardRef: true})
(LinceResultsKappaLauncher);