import React from 'react';
import styled from "styled-components";
import LayoutContent from "@iso/components/utility/layoutContent";
import {LayoutContentWrapper} from "@iso/components/utility/layoutWrapper.style";
import {Col} from "antd";

export function LinceLayoutContent(props) {
    return <LayoutContent {...props} className={"mainContent"}/>
}

export const StyledLayoutContentWrapper = styled(LayoutContentWrapper)`
    padding: 5px 5px!important;  
    .mainContent{
        padding: 5px 5px!important;
    }
`;


export const CenteredCol = styled(Col)`
    text-align: center;
`