import React from 'react';
import DataTable from "react-data-table-component";
import {faTh} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

const data = [
    {
        id: 1,
        title: 'Cutting Costs',
        by: 'me',
        lastOpened: 'Aug 7 9:52 AM',
    },
    {
        id: 2,
        title: 'Wedding Planner',
        by: 'me',
        lastOpened: 'Sept 14 2:52 PM',
    },
    {
        id: 3,
        title: 'Expense Tracker',
        by: 'me',
        lastOpened: 'Sept 12 2:41 PM',
    },
    {
        id: 4,
        title: 'Home Brew Water Calculator',
        by: 'me',
        lastOpened: 'Jube 3 5:45 PM',
    },
];
const columns = [
    {
        cell: () => <FontAwesomeIcon icon={faTh} style={{fill: '#43a047'}}/>,
        width: '56px', // custom width for icon button
        style: {
            borderBottom: '1px solid #FFFFFF',
            marginBottom: '-1px',
        },
    },
    {
        name: 'Title',
        selector: 'title',
        sortable: true,
        grow: 2,
        style: {
            color: '#202124',
            fontSize: '14px',
            fontWeight: 500,
        },
    },
    {
        name: 'Owner',
        selector: 'by',
        sortable: true,
        style: {
            color: 'rgba(0,0,0,.54)',
        },
    }
];

const customStyles = {
    headRow: {
        style: {
            border: 'none',
        },
    },
    headCells: {
        style: {
            color: '#202124',
            fontSize: '14px',
        },
    },
    rows: {
        highlightOnHoverStyle: {
            backgroundColor: 'rgb(230, 244, 244)',
            borderBottomColor: '#FFFFFF',
            borderRadius: '25px',
            outline: '1px solid #FFFFFF',
        },
    },
    pagination: {
        style: {
            border: 'none',
        },
    },
};



export function getTableIconCol() {
    return {
        cell: () => <FontAwesomeIcon icon={faTh} style={{fill: '#43a047'}}/>,
        width: '56px', // custom width for icon button
        style: {
            borderBottom: '1px solid #FFFFFF',
            marginBottom: '-1px',
        },
    }
}

export function getTableHeaderCol(title, selector) {
    return {
        name: title,
        selector: selector,
        sortable: true,
        grow: 2,
        style: {
            color: '#202124',
            fontSize: '14px',
            fontWeight: 500,
        }
    }
}

export function getTableRowCol(title, selector) {
    return {
        name: title,
        selector: selector,
        sortable: true,
        style: {
            color: 'rgba(0,0,0,.54)',
        },
    }
}

function LinceResultsTable(props) {
    return <DataTable
        responsive={true}
        dense={props.dense}
        title={props.title}
        columns={props.columns ? props.columns : columns}
        data={props.data ? props.data : data}
        customStyles={customStyles}
        highlightOnHover
        pointerOnHover
    />
}
LinceResultsTable.defaultProps ={
    responsive:true,
    dense:true
};/*
LinceResultsTable.PropTypes = {
    title: PropTypes.string,
    responsive: PropTypes.bool,
    dense: PropTypes.bool,
    columns: PropTypes.array,
    data: PropTypes.array,
};*/
export default LinceResultsTable;