import React from 'react';
import PropTypes from "prop-types";
import LinceResultsKappaLauncher from "../../components/lince/LinceResultsKappaLauncher";
import {Provider} from 'react-redux';
import {store} from '../../redux/store';
import '../../config/i18n';
import {Trans} from 'react-i18next';
import {Col, Menu, Row} from "antd";
import {LinceLayoutContent, StyledLayoutContentWrapper} from "../../components/lince/StyledLayoutComponents";
import PageHeader from "@iso/components/utility/pageHeader";
import ShowNotification from "../../components/utility/ShowNotification";
import LinceMenu from "./styled/LinceItems";
/*
GA code
 */
import siteConfig from '../../config/site.config';
import ReactGA from 'react-ga';
ReactGA.initialize(siteConfig.google.analyticsKey);
ReactGA.pageview(window.location.pathname + window.location.search);
/*
End GA code
 */
class ObserversApp extends React.Component {

    constructor(props) {
        super(props);
        this.state = {active: this.props.actions[0]};
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick = e => {
        if (this.props.actions) {
            let currentAction = {};
            this.props.actions.map(i => {
                if (i.key === e.key) {
                    return currentAction = i;
                }
                return null;
            });
            this.setState({active: currentAction});
            ShowNotification(currentAction.label, currentAction.info);
        }
        /*switch (e.key) {
            case "play":
                break;
            default:
                break;
        }*/
    }

    render() {
        let actions = this.props.actions;
        return (<Provider store={store}>
                <StyledLayoutContentWrapper>
                    <LinceLayoutContent>
                        <PageHeader>Observers: <Trans i18nKey="calculateResults">Calculate results</Trans></PageHeader>
                        <Row>
                            <Col span={24}>
                                <LinceMenu selectedKeys={[this.state.active.key]} onClick={this.handleClick}>
                                    {actions.map((action) => {
                                            return <Menu.Item key={action.key}> {action.label} </Menu.Item>
                                        }
                                    )}
                                </LinceMenu>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={24}>
                                <LinceResultsKappaLauncher currentAction={this.state.active}/>
                            </Col>
                        </Row>
                    </LinceLayoutContent>
                </StyledLayoutContentWrapper>
            </Provider>
        );
    }
}

ObserversApp.defaultProps = {
    actions: [{
        key: "kappa",
        label: "Conventional Kappa",
        info: "El índice Kappa te permite calcular los índices kappa de forma idéntica a como se hace en la primera versión de Lince (2012). \n Es tu responsabilidad que las escenas de observación sean idénticas."
    }, {
        key: "kappa-pro",
        label: "Fleiss Kappa",
        info: "Kappa pro es el nuevo cálculo de Lince en base al agoritmo de Fleiss’s Kappa κ (1971). Sólo apto para 2 observadores. \n Es tu responsabilidad que las escenas de observación sean idénticas."
    }, {
        key: "matrix",
        label: "Contingency matrix",//"Tabla de contingencia",
        info: "Esta funcionalidad te permitirá generar la tabla de contingencia entre 2 observadores para todos los criterios."
    }
        /* , {
        key: "krippendorf",
        label: "Krippendorf",
        info: "Krippendorff’s α (1980). Para 2 o más observadores. Se ofrecen cálculos de acuerdo y desacuerdo entre los observadores. \n Es tu responsabilidad que las escenas de observación sean idénticas."
        }*/
    ]
};
ObserversApp.propTypes = {
    actions: PropTypes.array.isRequired
};
export default ObserversApp;
