import React, {Component} from 'react';
import PropTypes from "prop-types";
import LinceResultsTable, {getTableIconCol, getTableRowCol} from "./LinceResultsTable";
import {Card} from "antd";
import styled from 'styled-components';

const LinceCard = styled(Card)`
    .ant-card-body{
        padding-left:0px;
        padding-right:0px;
    }
`;

class LinceResultsContingencyMatrix extends Component {
    constructor(props) {
        super(props);
        //this._renderContingencyRow = this._renderContingencyRow.bind(this);
        this._renderContingencyTable = this._renderContingencyTable.bind(this);
    }

    /*  _renderContingencyRow(code, index, rowValue) {
          const keyVal = "elem" + code + index;
          return <tr key={keyVal}>
              {rowValue.map((col, i) => {
                  return <td key={i + keyVal + "_" + i + "_" + col}>{col}</td>
              })}
          </tr>
      }
  */
    _renderContingencyTable(item) {
        const colsHeader = item.contingencyMatrix[0];
        let cols = [
            getTableIconCol(),
        ];
        colsHeader.forEach((col, idx) => {
            cols.push({
                ...getTableRowCol(col, "")
                //, width: "80px"
                , sortable: false
                , cell: (i) => {
                    return <span>{i[idx]}</span>
                }
            });
        });
        let data = item.contingencyMatrix.slice(1, item.contingencyMatrix.length);
        return <LinceResultsTable
            title={item.criteria.name}
            columns={cols}
            data={data}
        />
        /* <table className="table table-bordered table-hover" key={"k" + item.criteria.name}>
            <thead>
            <tr>
                <th>
                    <span className="label label-sm label-info">{item.criteria.name}</span>
                </th>
            </tr>
            </thead>
            <tbody>
            {item.contingencyMatrix.map((elem, i) => {
                return this._renderContingencyRow(item.criteria.name, i, elem)
            })}
            </tbody>
        </table>*/

    }

    render() {
        return <LinceCard title={this.props.title}>
            {this.props.content.map(i => {
                return this._renderContingencyTable((i))
            })}
        </LinceCard>
        /*<div className="todo-tasks-container">
            <div className="todo-head">
                <h3><i className="icon-arrow-left-circle"/>
                    <span>{this.props.title}</span></h3>
            </div>
            {this.props.content.map(i => {
                return this._renderContingencyTable((i))
            })}
        </div>*/
    }
}

LinceResultsContingencyMatrix.propTypes = {
    title: PropTypes.string,
    content: PropTypes.array
};

LinceResultsContingencyMatrix.defaultProps = {
    title: "Tablas de contingencia",
    content: []
};
export default (LinceResultsContingencyMatrix);
