import React, {Component} from 'react';
import PropTypes from "prop-types";
import LinceResultsContingencyMatrix from "./LinceResultsContingencyMatrix";
import {Trans} from 'react-i18next';
import {Card} from "antd";
import StickerWidget from "@iso/containers/Widgets/Sticker/StickerWidget";
import IsoWidgetsWrapper from "@iso/containers/Widgets/WidgetsWrapper";
import LinceRegisterExternal from "./LinceRegisterExternal";
import LinceResultsTable, {getTableHeaderCol, getTableIconCol, getTableRowCol} from "./LinceResultsTable";

class LinceResultsMainPanel extends Component {
    constructor(props) {
        super(props);
        this.nonErrValue = this.nonErrValue.bind(this);
    }

    nonErrValue(value) {
        return (value && !isNaN(value)) ? value.toPrecision(6) : "-";
    }

    render() {
        if (!this.props.showResults) {
            let elements = [];
            if (this.props.registerContent) {
                elements = this.props.registerContent;
            }
            const size = (null !== elements) ? elements.length : 0;
            const research = this.props.name;
            if (size > 0 || research !== "") {
                return (
                    <Card>
                        <h3><span className="todo-grey"><Trans
                            i18nKey="results.observationFrom">Observación de</Trans>&#160;
                                </span>{research}</h3>
                        <p className="todo-inline">{size} <Trans i18nKey="scenes">Escenas</Trans></p>
                        <LinceRegisterExternal register={elements} fullMode={false}/>
                    </Card>

                );
            } else {
                return <Card title={<Trans
                    i18nKey="results.selectRegister">Selecciona un registro a visualizar</Trans>}>
                    <IsoWidgetsWrapper>
                        <StickerWidget text={"Selecciona en el panel izquierdo"}
                                       number={0}
                                       icon={"ion-wand"}
                                       fontColor={"#ffffff"}
                                       bgColor={"#42A5F6"}
                        />
                    </IsoWidgetsWrapper>
                </Card>
            }
        } else {

            if (this.props.currentAction.key === "kappa") {
                const cols = [
                    getTableIconCol(),
                    getTableHeaderCol("Criteria", "key"),
                    {
                        ...getTableRowCol("Value", "valor"), cell: (i) => {
                            return <span>{this.nonErrValue(i.value)}</span>
                        }
                    }
                ];
                return <Card>
                    <LinceResultsTable
                        title={<Trans i18nKey="results.kappaResult">Resultado de cálculo del índice Kappa</Trans>}
                        columns={cols}
                        data={this.props.kappaResult}
                    />
                </Card>
            }
            if (this.props.currentAction.key === "kappa-pro") {
                const cols = [
                    getTableIconCol(),
                    {
                        ...getTableHeaderCol("Criteria", "key"), cell: (i) => {
                            return <span>{i.criteria.code}</span>
                        }
                    },
                    {
                        ...getTableRowCol("Agreement", "agreement"), cell: (i) => {
                            return <span>{this.nonErrValue(i.agreement)}</span>
                        }
                    },
                    {
                        ...getTableRowCol("Expected disagreement", "expectedDisagreement"), cell: (i) => {
                            return <span>{this.nonErrValue(i.expectedDisagreement)}</span>
                        }
                    },
                    {
                        ...getTableRowCol("Observed disagreement", "observedDisagreement"), cell: (i) => {
                            return <span>{this.nonErrValue(i.observedDisagreement)}</span>
                        }
                    }
                ];
                return <Card>
                    <LinceResultsTable
                        title={<Trans i18nKey="results.fleissKappa">Resultado de Índice Fleiss’s κ (1971)</Trans>}
                        columns={cols}
                        data={this.props.kappaProResult}
                    />
                </Card>
            }
            if (this.props.currentAction.key === "matrix") {
                return <LinceResultsContingencyMatrix content={this.props.contingencyMatrix}/>
            }
            return <div className="todo-tasks-container">
                <div className="todo-head">
                    <h3><i className="icon-arrow-left-circle"/>
                        <span><Trans
                            i18nKey="results.pressExecute">Presiona ejecutar para ver un resultado</Trans></span></h3>
                </div>
            </div>
        }
    }
}

LinceResultsMainPanel.propTypes = {
    name: PropTypes.string,
    showResults: PropTypes.bool,
    registerContent: PropTypes.array,
    kappaResult: PropTypes.array,
    kappaProResult: PropTypes.array,
    contingencyMatrix: PropTypes.array,
    currentAction: PropTypes.object,
};

LinceResultsMainPanel.defaultProps = {
    showResults: false,
    content: []
};
export default (LinceResultsMainPanel);
